"use client"
import {  useEffect, useState } from "react";
export default function Home() {
type Expense = {
  amount:number;
  category:string;
}

type Summary={
  total:number;
  by_category:{[key:string]:number}
}

  const [amount, setAmount] = useState('');
  const [category, setCategory] = useState('');
  const [date, setDate] = useState(new Date().toISOString().slice(0,10));
  const [todayExpenses, setTodayExpenses] = useState<Expense[]>([])
  const [summary, setSummary] = useState<Summary>({total:0, by_category:{}});

useEffect(() =>{
fetchToday();
fetchSummary();
},[]);

const fetchToday = async () =>{
  const res = await fetch('https://localhost:5000/expenses/today')
  const data = await res.json();
  setTodayExpenses(data);
}

const fetchSummary = async () =>{
  const res = await fetch('https://localhost:5000/expenses/summary');
  const data = await res.json();
  setSummary(data);
}

const handleSubmit = async (e:any) =>{
  e.preventDefault();
  await fetch('https://localhost:5000/expenses',{
    method:'POST',
    headers:{'Content-Type': 'application/json'},
    body: JSON.stringify({amount: parseFloat(amount),category,date})

  });
  setAmount('');
  fetchToday();
  fetchSummary();
};
  
  return (
   <>
   <div className="container">
   <h1>Kalkulator Finansów</h1>
   <form onSubmit={handleSubmit}>
   <input type="number" placeholder="Kwota" value={amount} 
   onChange={(e)=>setCategory(e.target.value)} required/>
   <select value={category} onChange={(e)=>setCategory(e.target.value)}>
   <option value="Jedzenie">Jedznie</option>
   <option value="Paliwo">Paliwo</option>
   <option value="Ubrania">Ubrania</option>
   <option value="Rozrywka">Rozrywka</option>

   </select>
   <input
   type="date"
   value={date}
   onChange={(e)=>setDate(e.target.value)}
   ></input>
   <button type="submit">Dodaj wydatek</button>
   </form>
   
   <div className="summary">
   
   <h2>Dzisiejsze Wydatki</h2>
   <ul>
   {todayExpenses.map((exp, idx) => (
          <li key={idx}>{exp.amount} zł – {exp.category}</li>
        ))}
   </ul>
   

  <h2>Suma: {summary.total}zł</h2>

   </div>
   

   </div>
   
   </>
  );
}
